import * as React from 'react';
import './App.css';

import Form from './components/Form.react';
import Confirmation from './components/Confirmation.react';
import Nux from './components/Nux.react';
import { genGoToURL, genLinkText, Tabs } from './util/genCTALink';
import OptedOut from './components/OptedOut.react';
import OptOutConfirmationStep from './components/OptOutConfirmationStep.react';

import { AUTO_CLOSE_CONFIG } from './components/AutoCloseOption.react';

import {
  load,
  persist,
  genClear,
  gen,
  genv,
  genSet,
} from './graphql/LocalStorage.js';
import { GOALS_QUERY, ROOT_QUERY } from './graphql/PersistentDocumentIds';
import { genGraphQL, genCachedGraphQL } from './graphql/GraphQL';
import genLog from './graphql/genLog';
import useEffectOnce from './util/useEffectOnce.react';
import { useState, useEffect, useCallback, useRef } from 'react';
import initErrorLogging from './util/initErrorLogging';

const SETTINGS_KEY = 'oneoneext/settings/v1';
const CONFIRMATION_VALUES_KEY = 'oneone_persisted_confirmation_values';

const TEN_MINUTES = 10 * 60;
const THIRTY_MINUTES = 30 * 60;
const IFRAME_DELAY_IN_MS = 80;

export type AppSettings = $ReadOnly<{|
  showNux: ?boolean,
  optedOut: ?boolean,
|}>;

export default function App(): React.MixedElement {
  initErrorLogging();
  const settingsRef = useRef(null);
  const [submittedDirectReportDetails, setSubmittedDirectReportDetails] =
    useState(null);
  const [goals, setGoals] = useState([]);
  const [hasGoalsAccess, setHasGoalsAccess] = useState(false);
  const [hasMgrLandingAccess, setHasMgrLandingAccess] = useState(false);
  const [showOptOutConfirmation, setShowOptOutConfirmation] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [hasOptedOut, setHasOptedOut] = useState(false);
  const [showNux, setShowNux] = useState(false);
  const [teamDetails, setTeamDetails] = useState([]);
  const [xfnDetails, setXFNDetails] = useState([]);
  const [canUseIframe, setCanUseIframe] = useState(false);
  const [secretAccessKey, setSecretAccessKey] = useState(null);

  const markHasOptedOut = useCallback((choice: boolean) => {
    const newSettings = {
      ...settingsRef.current,
      optedOut: choice,
    };
    settingsRef.current = newSettings;
    setHasOptedOut(choice);
    genSet(SETTINGS_KEY, newSettings);
  }, []);

  const markShowNux = useCallback((choice: boolean) => {
    const newSettings = {
      ...settingsRef.current,
      showNux: choice,
    };
    settingsRef.current = newSettings;
    setShowNux(choice);
    genSet(SETTINGS_KEY, newSettings);
  }, []);

  useEffect(() => {
    if (submittedDirectReportDetails == null) {
      genv(AUTO_CLOSE_CONFIG).then((config) => {
        if (config?.AUTO_CLOSE_CONFIG !== true) {
          load(CONFIRMATION_VALUES_KEY, setSubmittedDirectReportDetails);
        }
      });
    } else {
      persist(
        CONFIRMATION_VALUES_KEY,
        submittedDirectReportDetails,
        TEN_MINUTES
      );
    }
  }, [submittedDirectReportDetails]);

  useEffectOnce(() => {
    genLog({ event: 'IA_ACTION_ONE_ONE_CHROME_EXTENSION_OPEN' });
    gen(SETTINGS_KEY).then((storedSettings: ?AppSettings) => {
      settingsRef.current = storedSettings;
      setHasOptedOut(storedSettings?.optedOut === true);
      // if it's null, default to showing the NUX
      setShowNux(
        storedSettings == null ||
          storedSettings.showNux == null ||
          storedSettings.showNux === true
      );
      genGraphQL(GOALS_QUERY).then((data) => {
        setGoals(data?.one_one_extension_root?.viewer_goals ?? []);
      });
      genCachedGraphQL(
        ROOT_QUERY,
        { gk_name: 'one_on_one_chrome_ext_iframe' },
        THIRTY_MINUTES,
        (data, wasCached) => {
          const manager = data?.one_one_extension_root?.manager;
          const teamMembers = data?.one_one_extension_root?.teamMember ?? [];
          const xfnPartners = data?.one_one_extension_root?.xfnPartners ?? [];
          teamMembers.sort((a, b) =>
            (a.firstName ?? '').localeCompare(b.firstName)
          );
          xfnPartners.sort((a, b) =>
            (a.firstName ?? '').localeCompare(b.firstName)
          );
          setHasGoalsAccess(
            data?.one_one_extension_root?.viewer_has_goals_access === true
          );
          setHasMgrLandingAccess(
            data?.one_one_extension_root?.viewer_has_mgrlanding_access === true
          );
          // We should update the local storage with the server values
          // only it's fresh data from the server, otherwise we might
          // risk overwritting some recent change from the user
          if (!wasCached) {
            markHasOptedOut(data?.one_one_extension_root?.hasOptedOut === true);
          }
          setTeamDetails(format([manager, ...teamMembers]));
          setXFNDetails(format(xfnPartners));
          setIsLoading(false);
          setCanUseIframe(data?.one_one_extension_root?.viewer_in_gk);
          setSecretAccessKey(data?.one_one_extension_root?.secret_access_key);
        }
      );
    });
  });

  const title = hasMgrLandingAccess ? 'Team@' : '1:1 Tool';

  const [iFrameURL, setIFrameURL] = useState('about:blank');
  const [isIFrameLoading, setIsIFrameLoading] = useState(true);
  const iframeRef = useRef(null);
  useEffect(() => {
    if (!canUseIframe || !secretAccessKey) {
      return;
    }
    const iframeURL = `https://www.internalfb.com/intern/conversations/iframe_embed/${encodeURIComponent(
      secretAccessKey
    )}`;

    // NOTE (asharifr): Loading an iframe's content lags the popup from appearing.
    // We use a small timeout to make the extension more responsive, by allowing
    // enough time for rendering the loading UI.
    // I have tried using requestAnimationFrame, refs, and OnLoad listeners, but
    // they all suffer the same problem of lagging the popover.
    const timeout = setTimeout(() => {
      setIFrameURL(iframeURL);
    }, IFRAME_DELAY_IN_MS);
    return () => {
      clearTimeout(timeout);
    };
  }, [
    canUseIframe,
    secretAccessKey,
    setIsIFrameLoading,
    isIFrameLoading,
    setIFrameURL,
  ]);

  return canUseIframe ? (
    <div className="App">
      {isLoading || isIFrameLoading ? (
        <div className="AppLoader">
          <div>
            <h1>Loading 1:1...</h1>
            <div>
              <a
                href={genGoToURL(hasMgrLandingAccess)}
                target="_blank"
                rel="noopener noreferrer">
                <button className="button default-button" type="button">
                  <span className="button-text">
                    {genLinkText(hasMgrLandingAccess, Tabs.CONVERSATIONS)}
                  </span>
                </button>
              </a>
            </div>
          </div>
        </div>
      ) : null}
      <iframe
        ref={iframeRef}
        loading="lazy"
        onLoad={() => {
          setIsIFrameLoading(false);
        }}
        class="AppIFrame"
        title="iframe"
        style={{
          display: isIFrameLoading ? 'none' : 'block',
        }}
        src={iFrameURL}
        width="600"
        height="600"
      />
    </div>
  ) : (
    <div className="App">
      {showNux && (
        <Nux
          title={title}
          markShowNux={markShowNux}
          markHasOptedOut={markHasOptedOut}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {showOptOutConfirmation && (
        <OptOutConfirmationStep
          title={title}
          markShowNux={markShowNux}
          markHasOptedOut={markHasOptedOut}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {hasOptedOut && (
        <OptedOut title={title} markHasOptedOut={markHasOptedOut} />
      )}
      {submittedDirectReportDetails == null && !showNux && !hasOptedOut && (
        <Form
          title={title}
          isInitialRequestLoading={isLoading}
          setSubmittedDirectReportDetails={setSubmittedDirectReportDetails}
          goals={goals}
          hasGoalsAccess={hasGoalsAccess}
          hasMgrLandingAccess={hasMgrLandingAccess}
          setHasMgrLandingAccess={setHasMgrLandingAccess}
          markShowNux={markShowNux}
          teamDetails={teamDetails}
          xfnDetails={xfnDetails}
          setShowOptOutConfirmation={setShowOptOutConfirmation}
        />
      )}
      {submittedDirectReportDetails != null && !showNux && !hasOptedOut && (
        <Confirmation
          submittedDirectReportDetails={submittedDirectReportDetails}
          hasMgrLandingAccess={hasMgrLandingAccess}
          clearData={() => {
            genClear(CONFIRMATION_VALUES_KEY);
            setSubmittedDirectReportDetails(null);
          }}
        />
      )}
    </div>
  );
}

function format(employees) {
  return employees
    .filter((emp) => emp != null)
    .map((emp) => {
      return {
        employeeFBID: emp.fbid,
        firstName: emp.firstName,
        photo: emp.profilePicture?.uri,
      };
    });
}
