export const Tabs = {
  CONVERSATIONS: 'CONVERSATIONS',
  GOALS: 'GOALS',
};

export function genGoToURL(
  hasMgrLandingAccess: boolean,
  tab: $Keys<typeof Tabs> = Tabs.CONVERSATIONS,
  employeeID?: ?string
): string {
  if (tab === Tabs.GOALS) {
    return 'https://fburl.com/perf_goals';
  }
  const url = hasMgrLandingAccess
    ? getMgrlandingPath(employeeID, tab)
    : getOneOnePath(employeeID);
  return url;
}

function getOneOnePath(employeeID: ?string) {
  const oneOnePath = `https://www.internalfb.com/me/conversations/?referrer=ONEONE_EXTENSION${
    employeeID == null ? '' : `&second_employee_id=${employeeID}`
  }`;
  return oneOnePath;
}

function getMgrlandingPath(employeeID: ?string, tab: $Keys<typeof Tabs>) {
  const managerURL = 'https://www.internalfb.com/me/conversations/';

  switch (tab) {
    case Tabs.CONVERSATIONS:
    default:
      if (employeeID == null) {
        return managerURL;
      }
      return managerURL + '?second_employee_id=' + employeeID;
  }
}

export function genLinkText(
  hasMgrLandingAccess: boolean,
  tab: $Keys<typeof Tabs>,
  employeeName?: ?string
): string {
  if (employeeName != null) {
    return `Go to 1:1 with ${employeeName}`;
  }
  switch (tab) {
    case Tabs.GOALS: {
      // employeeName should be null when tab === GOALS
      return 'Go to Goals';
    }
    case Tabs.CONVERSATIONS:
    default: {
      return hasMgrLandingAccess ? 'Go to Team@' : 'Go to 1:1 Tool';
    }
  }
}
