import * as React from 'react';

export type ToastType = 'Failure' | 'Success';

type Props = {
  onDismiss: () => void,
  text: string,
  type: ToastType
};

export default function Toast(props: Props): React.MixedElement {
  return (
    <div className="toast-container">
      <div className={`toast ${props.type.toLowerCase()}`}>
        <img
          alt=""
          className="toast-icon"
          key={props.type}
          src={`${
            props.type === 'Success'
              ? './checkmark-circle-green-solid.png'
              : './warning-sign.png'
          }`}
        />{props.text}
        <button
          className="toast-close-button"
          onClick={e => {
            props.onDismiss();
          }}>
          
        </button>
      </div>
    </div>
  );
}
