import React from 'react';

import Checkbox  from './Checkbox.react';
import useEffectOnce from '../util/useEffectOnce.react';
import {genv, genSet} from '../graphql/LocalStorage';
import genLog from '../graphql/genLog';

type Props = $ReadOnly<{|
  clearData: () => void,
|}>;

export const AUTO_CLOSE_CONFIG = 'AUTO_CLOSE_CONFIG';
const AUTO_CLOSE_DIALOG_SECONDS = 2;
function AutoCloseOption({clearData}: Props): React.component {
  const [shouldAutoClose, setAutoClose] = React.useState(false);
  const [seconds, setSeconds] = React.useState(AUTO_CLOSE_DIALOG_SECONDS);
  const autoCloseText = 'Auto close' + (shouldAutoClose ? ' in ' + seconds : '');
  useEffectOnce(() => {
    genv(AUTO_CLOSE_CONFIG).then(config => {
      setAutoClose(config?.AUTO_CLOSE_CONFIG ?? false);
    });
  });
  const toggleClick = React.useCallback(() => {
    const value = !shouldAutoClose;
    genSet(AUTO_CLOSE_CONFIG, value);
    genLog({
      event: 'IA_ACTION_ONE_ONE_CHROME_EXTENSION_AUTO_CLOSE_TOGGLE',
      payload: [{key: 'new_value', value: String(value)}],
    });
    setAutoClose(value);
    setSeconds(AUTO_CLOSE_DIALOG_SECONDS);
  }, [shouldAutoClose, setAutoClose, setSeconds]);
  React.useEffect(() => {
    let interval = null;
    if (shouldAutoClose) {
      if(seconds === 0) {
        clearData();
        genLog({
          event: 'IA_ACTION_ONE_ONE_CHROME_EXTENSION_AUTO_CLOSED',
        });
        window.close();
      }
      interval = setInterval(() => {
        setSeconds(seconds => seconds - 1);
      }, 1000);
    } else if (!shouldAutoClose && seconds !== 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [shouldAutoClose, seconds, setSeconds, clearData]);

  return (
    <Checkbox value={shouldAutoClose} text={autoCloseText} onClick={toggleClick}/>
  );
}

export default AutoCloseOption;
