import * as React from 'react';

import { genGoToURL, genLinkText, Tabs } from '../util/genCTALink';
import genLog from '../graphql/genLog';

type Props = $ReadOnly<{|
  employeeFBID?: ?string,
  employeeName?: ?string,
  hasMgrLandingAccess: boolean,
  usePrimary: boolean,
  tab?: $Keys<typeof Tabs>,
|}>;

export default function LinkToTool({
  employeeFBID,
  employeeName,
  hasMgrLandingAccess,
  tab = Tabs.CONVERSATIONS,
  usePrimary,
}: Props): React.MixedElement {
  const linkText = genLinkText(hasMgrLandingAccess, tab, employeeName);
  const ctaLink = genGoToURL(hasMgrLandingAccess, tab, employeeFBID);
  const buttonClassName = usePrimary
    ? 'button primary-button'
    : 'button flat-button align-left';
  const imageSource = usePrimary ? 'goto.png' : 'go-to_outline_24.png';
  return (
    <a
      href={ctaLink}
      onClick={() => {
        genLog({
          event: 'ONE_ONE_CHROME_EXTENSION_LINK_CLICK',
          payload: [
            { key: 'link', value: ctaLink },
            { key: 'text', value: linkText },
          ],
        });
      }}
      target="_blank"
      rel="noopener noreferrer">
      <button className={buttonClassName} type="button">
        <div className="button-container">
          <img src={imageSource} alt="" className="button-icon" />
          <span className="button-text">{linkText}</span>
        </div>
      </button>
    </a>
  );
}
