import * as React from 'react';

type TextType = 'Body' | 'Label' | 'Supporting' | 'Title';

type Props = { children: React.Node, type: TextType };

function Text({ children, type }: Props): React.MixedElement {
  switch (type) {
    case 'Label':
      return <p className="text-label">{children}</p>;
    case 'Supporting':
      return <p className="text-supporting">{children}</p>;
    case 'Title':
      return <p className="text-title">{children}</p>;
    case 'Body':
    default:
      return <p>{children}</p>;
  }
}

export default Text;
