import * as React from 'react';
import { useState } from 'react';

import genLog from '../graphql/genLog';

type Props = $ReadOnly<{|
  tabs: Array<{
    title: React.Node,
    content: React.Node,
    name?: string,
  }>
|}>;

export default function TabManager({ tabs }: Props): React.MixedElement {
  const [activeTab, setActiveTab] = useState<number>(0);

  return (
    <>
      <div className="tab-container">
        {tabs.map((tab, index) => (
          <div
            className={`tab-item ${activeTab === index ? 'selected' : ''}`}
            onClick={() => {
              setActiveTab(index);
              genLog({
                event: 'ONE_ONE_CHROME_EXTENSION_TAB_CHANGE',
                payload: [{key: 'title', value: tab?.name ?? 'unknown'}]
              });
            }}>
            {tab.title}
          </div>
        ))}
      </div>
      {tabs[activeTab]['content']}
    </>
  );
}
