import * as React from 'react';

type Props = $ReadOnly<{|
  children?: React.Node,
  classLabel?: string
|}>;

function Spinner({ children, classLabel }: Props): React.MixedElement {
  return (
    <img
      alt=""
      class="spinner"
      src="data:image/png;base64,"
    />
  );
}

export default Spinner;
