import type { Goal } from './Form.react';
import type { ToastType } from './Toast.react';

import * as React from 'react';

import { genGraphQL } from '../graphql/GraphQL';
import {
  gen,
  genClear,
  genClearTextAreaContent
} from '../graphql/LocalStorage';
import genLog from '../graphql/genLog';
import LinkToTool from './LinkToTool.react';
import PersistedTextField from './PersistedTextField.react';
import PersistedTextFieldHeader from './PersistedTextFieldHeader.react';
import Section from './Section.react';
import Select from './Select.react';
import Text from './Text.react';
import Toast from './Toast.react';

import { Tabs } from '../util/genCTALink';
import { GOALS_CREATE_MUTATION } from '../graphql/PersistentDocumentIds';
import {
  GOAL_FIELD_LOCAL_KEY,
  PAGE_URL_LOCAL_KEY
} from '../util/LocalDataStoreKeys';

type Props = $ReadOnly<{|
  goals: $ReadOnlyArray<Goal>,
  hasMgrLandingAccess: boolean
|}>;

const GOAL_STATUSES = {
  UNASSIGNED: 'UNASSIGNED',
  NONE: 'NONE'
};

function GoalsContainer({
  goals,
  hasMgrLandingAccess
}: Props): React.MixedElement {
  const [topic, setTopic] = React.useState('');
  const [isLoading, setIsLoading] = React.useState(false);
  const [pageURL, setPageURL] = React.useState(null);
  const [includePageURL, setIncludePageURL] = React.useState(false);
  const [selectedGoalID, setSelectedGoalID] = React.useState('');
  const [showToast, setShowToast] = React.useState<boolean>(false);
  const [toastType, setToastType] = React.useState<ToastType>('Success');
  const toastText = {
    Success: 'Goal created successfully!',
    Failure: 'An error occured, please try again.'
  };

  React.useEffect(() => {
    gen(PAGE_URL_LOCAL_KEY).then((storedPageURL: ?string) => {
      if (storedPageURL !== null) {
        setPageURL(storedPageURL);
      }
    });
  });

  const addGoalCallback = React.useCallback(() => {
    if (isLoading) {
      return;
    }
    const isUnattachedGoal = selectedGoalID === '';

    setIsLoading(true);
    genGraphQL(GOALS_CREATE_MUTATION, {
      parent_goal_id: isUnattachedGoal ? null : selectedGoalID,
      status: isUnattachedGoal ? GOAL_STATUSES.UNASSIGNED : GOAL_STATUSES.NONE,
      content: topic,
      child_goals_data: [], // chrome extension doesn't currently support creating top level goals
      goal_url: includePageURL ? pageURL : null,
      product: 'PMT'
    })
      .then(data => {
        const payload = [
          { key: 'include_url', value: includePageURL ? 'true' : 'false' },
          {
            key: 'has_parent',
            value: isUnattachedGoal ? 'false' : 'true'
          }
        ];
        if (data?.xfb_pmt_goal_create?.id != null) {
          setToastType('Success');
          setSelectedGoalID('');
          setTopic('');
          genClear(GOAL_FIELD_LOCAL_KEY);
          genLog({
            event: 'ONE_ONE_CHROME_EXTENSION_CREATE_GOAL_SUCCESS',
            payload,
          });
        } else {
          setToastType('Failure');
          // leave the textbox's content so user can try again
          genLog({
            event: 'ONE_ONE_CHROME_EXTENSION_CREATE_GOAL_FAILURE',
            payload,
          });
        }
      })
      .finally(() => {
        setIsLoading(false);
        setShowToast(true);
      });
  }, [includePageURL, isLoading, pageURL, selectedGoalID, topic]);

  return (
    <React.Fragment key={`${toastType}${String(showToast)}`}>
      {showToast && (
        <Toast
          onDismiss={() => {
            setShowToast(false);
          }}
          text={toastText[toastType]}
          type={toastType}
          visible={showToast}
        />
      )}
      <Section>
        <PersistedTextFieldHeader
          header="Capture impact and progress toward performance goals."
          topic={topic}
          setTopic={setTopic}
          setIncludePageURL={setIncludePageURL}
        />
        <PersistedTextField
          placeholder="Add a link and track your impact"
          topic={topic}
          topicKey={GOAL_FIELD_LOCAL_KEY}
          setTopic={setTopic}
        />
      </Section>
      <Section>
        <Select
          name="goal-select"
          onChange={setSelectedGoalID}
          options={[
            { text: 'none', value: '' },
            ...goals.map(goal => ({ text: goal.content, value: goal.id }))
          ]}
          style={{maxWidth: 300}}
        />
      </Section>
      <Section classLabel="section-vertical-centered">
        <img
          src="unlocked-icon.png"
          alt=""
          width="16px"
          height="16px"
          style={{ marginRight: '4px' }}
        />
        <Text type="Supporting">
          If you connect to a goal, this impact becomes visible to your manager.
        </Text>
      </Section>
      <Section classLabel="footer-section normal-section">
        <div className="control-container">
          <LinkToTool
            hasMgrLandingAccess={hasMgrLandingAccess}
            tab={Tabs.GOALS}
            usePrimary={false}
          />
          <div>
            <button
              className="button default-button"
              type="button"
              onClick={() => {
                genClearTextAreaContent();
                genLog({
                  event: 'IA_ACTION_ONE_ONE_CHROME_EXTENSION_CANCEL_GOALS_TAB'
                });
                window.close();
              }}>
              <div className="button-container">
                <span className="button-text">Cancel</span>
              </div>
            </button>
            <button
              className="button primary-button"
              disabled={isLoading || topic.trim() === ''}
              type="button"
              onClick={() => {
                addGoalCallback();
              }}>
              <div className="button-container">
                <span className="button-text">Add</span>
              </div>
            </button>
          </div>
        </div>
      </Section>
    </React.Fragment>
  );
}

export default GoalsContainer;
